<template>
  <div :class="classes"
    @click="switchValue"
  >
    <span :class="['z-switch__inner', normal&&'is-normal']">
      <span v-if="insideText" class="z-switch__inside-text">{{curText}}</span>
    </span>
    <span v-if="!insideText" class="z-switch__label">{{curText}}</span>
  </div>
</template>

<script>
export default {
	name: 'ZSwitch',
	data() {
		return {
			curValue: '',
			curText: ''
		};
	},
	props: {
		value: {
			type: [Boolean, String, Number],
			default: false
		},
		activeValue: {
			type: [Boolean, String, Number],
			default: true
		},
		inactiveValue: {
			type: [Boolean, String, Number],
			default: false
		},
		activeText: String,
		inactiveText: String,
		insideText: Boolean,
		disabled: Boolean,
		normal: Boolean
	},
	computed: {
		classes () {
			return [
				'z-switch',
				'jusBetween-alignCenter',
				this.disabled && 'is-disabled',
				this.active && 'is-active'
			];
		},
		checked () {
			return this.value === this.activeValue;
		},
		active: {
			get () {
				return this.value === this.activeValue;
			},
			set (val) {
				this.curValue = val ? this.activeValue : this.inactiveValue;
				this.curText = !val ? this.activeText : this.inactiveText;
			}
		},
	},
	mounted () {
		this.active = this.value === this.activeValue;
	},
	methods: {
		switchValue () {
			this.active = !this.active;
			this.$emit('input', this.curValue);
			this.$emit('change', this.curValue);
		}
	}
};
</script>
